/* Subscrib Area css
============================================================================================ */
.subscrib_area {
  .subscrib_inner {
    border-bottom: 1px solid #e0e2e6;
    padding-bottom: 100px;
    .main_title {
      padding-bottom: 60px;
      p {
        max-width: 520px;
      }
    }
    .input-group {
      max-width: 525px;
      margin: auto;
      display: block;
      position: relative;
      width: 100%;
      height: 62px;
      input {
        height: 62px;
        padding: 0px 172px 0px 35px;
        border-radius: 31px;
        border: 1px solid #e0e2e6;
        border-right: 0px;
        box-shadow: none;
        outline: none;
        background: transparent;
        width: 100%;
        display: block;
        @include placeholder {
          font-family: $pop, FontAwesome;
          font-size: 18px;
          font-weight: normal;
          letter-spacing: 0.36px;
          color: $pfont;
        }
      }
      .input-group-btn {
        display: block;
        white-space: normal;
        position: absolute;
        right: 0px;
        width: 172px;
        border-radius: 31px;
        top: 0px;
        box-shadow: 0px 4px 12.35px 0.65px rgba(0, 0, 0, 0.12);
        .btn-default {
          height: 62px;
          padding: 0px;
          box-shadow: none !important;
          outline: none !important;
          border-radius: 0px;
          border: none;
          background: transparent;
          position: relative;
          width: 172px;
          text-align: center;
          border-radius: 31px;
          z-index: 3;
          color: #fff;
          font-family: $pop;
          letter-spacing: 0.48px;
          font-size: 24px;
          text-transform: uppercase;
          font-weight: normal;
          &:before {
            content: "";
            height: 100%;
            width: 100%;
            background: rgba(227, 204, 252, 1);
            background: -moz-linear-gradient(
              left,
              rgba(227, 204, 252, 1) 0%,
              rgba(143, 205, 252, 1) 100%
            );
            background: -webkit-gradient(
              left top,
              right top,
              color-stop(0%, rgba(227, 204, 252, 1)),
              color-stop(100%, rgba(143, 205, 252, 1))
            );
            background: -webkit-linear-gradient(
              left,
              rgba(227, 204, 252, 1) 0%,
              rgba(143, 205, 252, 1) 100%
            );
            background: -o-linear-gradient(
              left,
              rgba(227, 204, 252, 1) 0%,
              rgba(143, 205, 252, 1) 100%
            );
            background: -ms-linear-gradient(
              left,
              rgba(227, 204, 252, 1) 0%,
              rgba(143, 205, 252, 1) 100%
            );
            background: linear-gradient(
              to right,
              rgba(227, 204, 252, 1) 0%,
              rgba(143, 205, 252, 1) 100%
            );
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#e3ccfc', endColorstr='#8fcdfc', GradientType=1 );
            position: absolute;
            left: 0px;
            top: 0px;
            border-radius: 31px;
            z-index: -1;
          }
        }
      }
    }
  }
}
/* End Subscrib Area css
============================================================================================ */

