    padding-bottom: 55px;
    h3 {
      font-size: 66px;
      font-family: $mon;
      text-transform: uppercase;
      font-weight: normal;
    }
    h4 {
      font-size: 36px;
      font-family: $mon;
      font-weight: 300;
      padding-top: 10px;
    }
  }
}
.domain_search_box {
  max-width: 850px;
  margin: auto;
  background: #d9d9d9;
  padding: 15px 15px;
  display: flex;
  input {
    width: 66%;
    margin-right: 15px;
    height: 74px;
    border: none;
    box-shadow: none;
    background: #fff;
    border-radius: 0px;
    padding: 0px 20px;
    font-size: 16px;
    color: $pfont;
    font-family: $pop;
    box-shadow: 0px 4px 6.65px 0.35px rgba(0, 0, 0, 0.1);
    @include placeholder {
      font-size: 16px;
      color: $pfont;
      font-family: $pop;
    }
  }
  .select_dropdown {
    width: 16%;
    margin-right: 15px;
    border-radius: 0px;
    border: none;
    height: 74px;
    text-align: center !important;
    box-shadow: 0px 4px 6.65px 0.35px rgba(0, 0, 0, 0.1);
    .current {
      line-height: 74px;
      text-transform: lowercase;
      font-size: 16px;
      color: $pfont;
      font-family: $pop;
      text-align: center;
    }
    &:after {
      right: 35px;
    }
    .list {
      width: 100%;
      border-radius: 0px;
      li {
        font-weight: normal;
        font-size: 16px;
        color: $pfont;
        font-family: $pop;
      }
    }
  }
  .btn.btn-default {
    width: 18%;
    height: 74px;
    border: none;
    border-radius: 0px;
    box-shadow: 0px 4px 6.65px 0.35px rgba(0, 0, 0, 0.1);
    font-size: 16px;
    color: $pfont;
    font-family: $pop;
    outline: none !important;
    background: #fff;
  }
}
.domain_price_list {
  max-width: 780px;
  margin: auto;
  margin-top: 50px;
  ul {
    overflow: hidden;
    li {
      float: left;
      height: 98px;
      width: 98px;
      background: rgba(255, 255, 255, 0.5);
      border-radius: 50%;
      margin-right: 28px;
      padding: 9px;
      a {
        text-align: center;
        display: block;
        font-size: 16px;
        font-family: $pop;
        color: #fff;
        height: 80px;
        width: 80px;
        border-radius: 50%;
        span {
          display: block;
          padding-top: 14px;
        }
        &.red {
          background: #fc5a1a;
        }
        &.pest {
          background: #32bdab;
        }
        &.yellow {
          background: #ffb21a;
        }
        &.blue {
          background: #3498db;
        }
        &.green {
          background: #3cdb34;
        }
        &.pink {
          background: #fb67f2;
        }
      }
      &:nth-child(even) {
        margin-right: 50px;
      }
      &:last-child {
        margin-right: 0px;
      }
    }
  }
}
/* End Domain Search Area css
============================================================================================ */

