/* Feature Area css
============================================================================================ */
.app_feature_area {
  padding: 100px 0px;
  .main_title {
    padding-bottom: 55px;
  }
}
.app_feature_inner {
}
.app_round_image {
  position: relative;
  padding-top: 80px;
  text-align: center;
  .round_c {
    animation: 10s round_image infinite linear;
  }
  .app_mobile {
    position: absolute;
    top: 0px;
    left: 50%;
    transform: translateX(-50%);
  }
}
.app_feature_item_inner {
  text-align: right;
  padding-top: 245px;
  &.left_feature {
    text-align: left;
    .app_feature_item {
      .round_icon {
        left: -94px;
      }
    }
  }
}
.app_feature_item {
  margin-bottom: 70px;
  position: relative;
  h4 {
    font-size: 18px;
    color: $dip;
    font-family: $mon;
    letter-spacing: 0.36px;
    padding-bottom: 17px;
    @include transition;
  }
  p {
    font-size: 14px;
    line-height: 27px;
    color: $pfont;
    font-family: $pop;
  }
  &:last-child {
    margin-bottom: 0px;
  }
  .round_icon {
    height: 58px;
    width: 58px;
    text-align: center;
    line-height: 57px;
    border-radius: 50%;
    font-size: 24px;
    position: absolute;
    right: -94px;
    background: rgba(218, 202, 252, 1);
    background: -moz-linear-gradient(
      45deg,
      rgba(218, 202, 252, 1) 0%,
      rgba(158, 203, 252, 1) 100%
    );
    background: -webkit-gradient(
      left bottom,
      right top,
      color-stop(0%, rgba(218, 202, 252, 1)),
      color-stop(100%, rgba(158, 203, 252, 1))
    );
    background: -webkit-linear-gradient(
      45deg,
      rgba(218, 202, 252, 1) 0%,
      rgba(158, 203, 252, 1) 100%
    );
    background: -o-linear-gradient(
      45deg,
      rgba(218, 202, 252, 1) 0%,
      rgba(158, 203, 252, 1) 100%
    );
    background: -ms-linear-gradient(
      45deg,
      rgba(218, 202, 252, 1) 0%,
      rgba(158, 203, 252, 1) 100%
    );
    background: linear-gradient(
      45deg,
      rgba(218, 202, 252, 1) 0%,
      rgba(158, 203, 252, 1) 100%
    );
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#dacafc', endColorstr='#9ecbfc', GradientType=1 );
    z-index: 1;
    top: -28px;
    &:before {
      content: "";
      height: 56px;
      width: 56px;
      border-radius: 50%;
      background: #fff;
      position: absolute;
      right: 1px;
      top: 1px;
      z-index: 1;
    }
    &:after {
      content: "";
      width: 64px;
      height: 64px;
      border-radius: 50%;
      background: rgba(218, 202, 252, 1);
      background: -moz-linear-gradient(
        45deg,
        rgba(218, 202, 252, 0.4) 0%,
        rgba(158, 203, 252, 0.4) 100%
      );
      background: -webkit-gradient(
        left bottom,
        right top,
        color-stop(0%, rgba(218, 202, 252, 0.4)),
        color-stop(100%, rgba(158, 203, 252, 1))
      );
      background: -webkit-linear-gradient(
        45deg,
        rgba(218, 202, 252, 0.4) 0%,
        rgba(158, 203, 252, 0.4) 100%
      );
      background: -o-linear-gradient(
        45deg,
        rgba(218, 202, 252, 0.4) 0%,
        rgba(158, 203, 252, 0.4) 100%
      );
      background: -ms-linear-gradient(
        45deg,
        rgba(218, 202, 252, 0.4) 0%,
        rgba(158, 203, 252, 0.4) 100%
      );
      background: linear-gradient(
        45deg,
        rgba(218, 202, 252, 0.4) 0%,
        rgba(158, 203, 252, 0.4) 100%
      );
      filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#dacafc', endColorstr='#9ecbfc', GradientType=1 );
      position: absolute;
      top: -3px;
      left: -3px;
      z-index: -1;
      animation: runpulse 3s infinite linear;
      animation-delay: 1s;
    }
    i {
      background: -webkit-linear-gradient(left, #dbcafc, #9eccfc);
      background: linear-gradient(left, #dbcafc, #9eccfc);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      z-index: 3;
      position: relative;
    }
  }
  &.app_middle {
    max-width: 275px;
    margin: auto;
    text-align: center;
    padding-top: 50px;
    .round_icon {
      right: auto;
      left: 50%;
      top: -32px;
      transform: translateX(-50%);
    }
  }
  &:hover {
    h4 {
      background: -webkit-linear-gradient(left, #dbcafc, #9eccfc);
      background: linear-gradient(left, #dbcafc, #9eccfc);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }
}
/* End Feature Area css
============================================================================================ */

/* Choose Us Area css
============================================================================================ */
.choose_us_area {
  background: #eff1ff;
  min-height: 770px;
  position: relative;
  padding-top: 30px;
  margin-top: 90px;
  &:before {
    content: "";
    position: absolute;
    top: -94px;
    left: 50%;
    transform: translateX(-50%);
    // background: url(../img/triangle-shap-1.png) no-repeat scroll center center;
    background-size: cover;
    width: 100%;
    min-height: 94px;
  }
}
.left_choose_image {
  position: relative;
  min-height: 740px;
  overflow: hidden;
  ul {
    li {
      position: absolute;
      left: 0px;
      top: 140px;
      &:nth-child(2) {
        top: auto;
        bottom: -20px;
      }
      &:nth-child(3) {
        left: calc(50% - 280px);
        top: calc(50% - 70px);
      }
      &:nth-child(4) {
        top: auto;
        bottom: 40px;
        left: calc(50% - 220px);
      }
      &:nth-child(5) {
        top: -38px;
        left: calc(50% - 238px);
      }
      &:nth-child(6) {
        top: auto;
        bottom: 180px;
        left: calc(50% + 90px);
      }
      &:nth-child(7) {
        left: calc(50% + 75px);
        top: 64px;
      }
      &:nth-child(8) {
        left: auto;
        right: 112px;
        top: 260px;
      }
    }
  }
}

.right_choose_image {
  max-width: 570px;
  padding-right: 30px;
  padding-top: 90px;
}
.choose_item_inner {
  margin-top: 44px;
  .col-md-6 {
    &:nth-child(3) {
      .choose_item {
        margin-bottom: 0px;
      }
    }
    &:last-child {
      .choose_item {
        margin-bottom: 0px;
      }
    }
  }
  .choose_item {
    margin-bottom: 50px;
    .media {
      .media-left {
        padding-right: 15px;
        a {
          background: rgba(218, 202, 252, 1);
          background: -moz-linear-gradient(
            45deg,
            rgba(218, 202, 252, 1) 0%,
            rgba(158, 203, 252, 1) 100%
          );
          background: -webkit-gradient(
            left bottom,
            right top,
            color-stop(0%, rgba(218, 202, 252, 1)),
            color-stop(100%, rgba(158, 203, 252, 1))
          );
          background: -webkit-linear-gradient(
            45deg,
            rgba(218, 202, 252, 1) 0%,
            rgba(158, 203, 252, 1) 100%
          );
          background: -o-linear-gradient(
            45deg,
            rgba(218, 202, 252, 1) 0%,
            rgba(158, 203, 252, 1) 100%
          );
          background: -ms-linear-gradient(
            45deg,
            rgba(218, 202, 252, 1) 0%,
            rgba(158, 203, 252, 1) 100%
          );
          background: linear-gradient(
            45deg,
            rgba(218, 202, 252, 1) 0%,
            rgba(158, 203, 252, 1) 100%
          );
          filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#dacafc', endColorstr='#9ecbfc', GradientType=1 );
          height: 66px;
          width: 66px;
          text-align: center;
          border-radius: 50%;
          display: inline-block;
          position: relative;
          z-index: 2;
          &:before {
            content: "";
            height: 64px;
            width: 64px;
            position: absolute;
            left: 1px;
            top: 1px;
            background: #fff;
            z-index: -1;
            border-radius: 50%;
          }
        }
        i {
          font-size: 36px;
          background: -webkit-linear-gradient(left, #dbcafc, #9eccfc);
          background: linear-gradient(left, #dbcafc, #9eccfc);
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
          z-index: 3;
          position: relative;
          line-height: 61px;
        }
      }
      .media-body {
        vertical-align: middle;
        h4 {
          font-size: 18px;
          font-family: $mon;
          color: $dip;
          letter-spacing: 0.36px;
          padding-bottom: 5px;
        }
        p {
          font-size: 14px;
          font-family: $pop;
          color: $pfont;
          letter-spacing: 0.28px;
        }
      }
    }
  }
}

/* End Choose Us Area css
============================================================================================ */

