/* Question Area css
============================================================================================ */
.question_area {
  background: #eff1ff;
  padding: 100px 0px;
  .main_title {
    padding-bottom: 65px;
  }
}
.left_question_inner {
  .panel-group {
    .panel-default {
      border-radius: 0px;
      background: #fff;
      border: none;
      margin-top: 30px;
      box-shadow: 0px 3px 12.35px 0.65px rgba(0, 0, 0, 0.1);
      &:first-child {
        margin-top: 0px;
      }
      .panel-heading {
        padding: 0px;
        border: none;
        background: transparent;
        border-radius: 0px;
        .panel-title {
          a {
            font-size: 18px;
            color: $dip;
            font-family: $mon;
            font-weight: normal;
            line-height: 70px;
            padding: 0px 25px;
            display: block;
            position: relative;
            i {
              float: right;
              font-size: 12px;
              text-align: center;
              height: 20px;
              line-height: 20px;
              width: 20px;
              background: rgba(224, 212, 252, 1);
              background: -moz-linear-gradient(
                45deg,
                rgba(224, 212, 252, 1) 0%,
                rgba(177, 213, 252, 1) 100%
              );
              background: -webkit-gradient(
                left bottom,
                right top,
                color-stop(0%, rgba(224, 212, 252, 1)),
                color-stop(100%, rgba(177, 213, 252, 1))
              );
              background: -webkit-linear-gradient(
                45deg,
                rgba(224, 212, 252, 1) 0%,
                rgba(177, 213, 252, 1) 100%
              );
              background: -o-linear-gradient(
                45deg,
                rgba(224, 212, 252, 1) 0%,
                rgba(177, 213, 252, 1) 100%
              );
              background: -ms-linear-gradient(
                45deg,
                rgba(224, 212, 252, 1) 0%,
                rgba(177, 213, 252, 1) 100%
              );
              background: linear-gradient(
                45deg,
                rgba(224, 212, 252, 1) 0%,
                rgba(177, 213, 252, 1) 100%
              );
              filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#e0d4fc', endColorstr='#b1d5fc', GradientType=1 );
              border-radius: 50%;
              display: inline-block;
              position: absolute;
              top: 50%;
              right: 25px;
              transform: translateY(-50%);
              z-index: 3;
              &:after {
                content: "";
                height: 18px;
                width: 18px;
                background: #fff;
                position: absolute;
                left: 1px;
                top: 1px;
                border-radius: 50%;
                z-index: -1;
              }
              &:before {
                background: -webkit-linear-gradient(left, #dbcafc, #9eccfc);
                background: linear-gradient(left, #dbcafc, #9eccfc);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
              }
              & + i {
                display: none;
              }
            }
            &.collapsed {
              i {
                & + i {
                  display: inline-block;
                }
              }
            }
          }
        }
      }
      .panel-collapse {
        .panel-body {
          border: none;
          padding-top: 0px;
          padding-left: 25px;
          padding-right: 90px;
          padding-bottom: 35px;
          font-size: 14px;
          line-height: 27px;
          color: $pfont;
          font-family: $pop;
        }
      }
    }
  }
}
.right_question_img {
  text-align: center;
  img {
    max-width: 100%;
    display: inline-block;
  }
}
/* End Question Area css
============================================================================================ */

