/* Price Area css
============================================================================================ */
.price_table_area {
  background: #eff1ff;
  padding: 100px 0px;
  .main_title {
    padding-bottom: 65px;
  }
}
.price_table_inner {
  max-width: 917px;
  margin: auto;
  .price_item {
    width: calc(100% / 3);
    float: left;
    text-align: center;
    background: #fff;
    padding-bottom: 44px;
    overflow: hidden;
    .price_header {
      background: #d8cafc;
      padding: 44px 0px 30px 0px;
      h3 {
        font-size: 18px;
        text-transform: uppercase;
        font-family: $mon;
        font-weight: 300;
        color: $dip;
        background: #fff;
        display: block;
        max-width: 155px;
        text-align: center;
        margin: auto;
        line-height: 35px;
        letter-spacing: 0.36px;
        border-radius: 17.5px;
      }
      h2 {
        font-size: 50px;
        font-family: $mon;
        font-weight: bold;
        color: #fff;
        letter-spacing: 1px;
        padding-top: 8px;
        padding-bottom: 5px;
      }
      h6 {
        font-size: 18px;
        font-family: $mon;
        color: #fff;
        font-weight: 300;
        letter-spacing: 0.36px;
      }
    }
    ul {
      padding: 50px 0px;
      li {
        margin-bottom: 25px;
        a {
          font-size: 14px;
          font-family: $pop;
          color: $pfont;
        }
        &:last-child {
          margin-bottom: 0px;
        }
      }
    }
    &:nth-child(2) {
      box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.14);
      transform: scale(1.0001);
      padding-bottom: 60px;
      .price_header {
        background: rgba(218, 202, 252, 1);
        background: -moz-linear-gradient(
          left,
          rgba(218, 202, 252, 1) 0%,
          rgba(155, 204, 252, 1) 100%
        );
        background: -webkit-gradient(
          left top,
          right top,
          color-stop(0%, rgba(218, 202, 252, 1)),
          color-stop(100%, rgba(155, 204, 252, 1))
        );
        background: -webkit-linear-gradient(
          left,
          rgba(218, 202, 252, 1) 0%,
          rgba(155, 204, 252, 1) 100%
        );
        background: -o-linear-gradient(
          left,
          rgba(218, 202, 252, 1) 0%,
          rgba(155, 204, 252, 1) 100%
        );
        background: -ms-linear-gradient(
          left,
          rgba(218, 202, 252, 1) 0%,
          rgba(155, 204, 252, 1) 100%
        );
        background: linear-gradient(
          to right,
          rgba(218, 202, 252, 1) 0%,
          rgba(155, 204, 252, 1) 100%
        );
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#dacafc', endColorstr='#9bccfc', GradientType=1 );
      }
      .price_btn {
        background: rgba(218, 202, 252, 1);
        background: -moz-linear-gradient(
          left,
          rgba(218, 202, 252, 1) 0%,
          rgba(155, 204, 252, 1) 100%
        );
        background: -webkit-gradient(
          left top,
          right top,
          color-stop(0%, rgba(218, 202, 252, 1)),
          color-stop(100%, rgba(155, 204, 252, 1))
        );
        background: -webkit-linear-gradient(
          left,
          rgba(218, 202, 252, 1) 0%,
          rgba(155, 204, 252, 1) 100%
        );
        background: -o-linear-gradient(
          left,
          rgba(218, 202, 252, 1) 0%,
          rgba(155, 204, 252, 1) 100%
        );
        background: -ms-linear-gradient(
          left,
          rgba(218, 202, 252, 1) 0%,
          rgba(155, 204, 252, 1) 100%
        );
        background: linear-gradient(
          to right,
          rgba(218, 202, 252, 1) 0%,
          rgba(155, 204, 252, 1) 100%
        );
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#dacafc', endColorstr='#9bccfc', GradientType=1 );
      }
    }
  }
}
.price_btn {
  display: inline-block;
  text-align: center;
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 0.28px;
  padding: 0px 28px;
  background: #d9cafc;
  line-height: 35px;
  border-radius: 17.5px;
  color: #fff;
  &:hover {
    color: #fff;
  }
}
/* End Price Area css
============================================================================================ */

/* Server Price Area css
============================================================================================ */
.server_price_area {
  padding: 100px 0px;
}
.service_price_inner {
}
.server_price_item {
  text-align: center;
  box-shadow: 0px 0px 5px 0px rgba(6, 83, 129, 0.26);
  .pirce_head {
    text-align: center;
    background: #065381;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    min-height: 155px;
    padding-top: 28px;
    h4 {
      font-size: 24px;
      color: #fff;
      font-family: $hee;
      letter-spacing: 0.48px;
      margin-top: 15px;
    }
  }
  .pirce_body {
    border-bottom: 3px solid #064f7d;
    h5 {
      font-size: 23px;
      color: #065381;
      font-family: $hee;
      font-weight: 900;
      padding: 25px 0px 40px 0px;
    }
    ul {
      li {
        margin-bottom: 15px;
        a {
          font-size: 14px;
          font-family: $hee;
          color: #5f7b93;
          letter-spacing: 0.28px;
          span {
            font-weight: bold;
          }
        }
        &:last-child {
          margin-bottom: 0px;
        }
      }
    }
    .pirce_footer {
      padding: 50px 0px;
      a {
        font-size: 16px;
        color: #f1f7f5;
        font-family: $hee;
        font-weight: bold;
        display: inline-block;
        padding: 0px 35px;
        background: #6d89a1;
        line-height: 44px;
        letter-spacing: 0.32px;
        border-radius: 22px;
      }
    }
  }
  &.prime {
    box-shadow: 0px 0px 5px 0px rgba(255, 150, 0, 0.26);
    .pirce_head {
      background: #ff9600;
      h6 {
        background: #26547c;
        display: inline-block;
        padding: 0px 15px;
        border-radius: 7.5px;
        color: #fff;
        font-size: 10px;
        font-family: $hee;
        font-weight: 500;
        letter-spacing: 0.4px;
        line-height: 15px;
      }
    }
    .price-body {
      border-bottom: 3px solid #ff9600;
    }
  }
  &.ultimate {
    box-shadow: 0px 0px 5px 0px rgba(47, 210, 240, 0.26);
    .pirce_head {
      background: #2fd2f0;
    }
    .pirce_body {
      border-bottom: 3px solid #2fd2f0;
    }
  }
}

.price_item2 {
  h2 {
    text-align: center;
  }
  h3 {
    text-align: center;
  }
  .theme_btn {
    border-radius: 0px;
    text-align: center;
    display: block;
    width: 152px;
    margin: 40px auto 0px;
  }
  &.orange {
    &:before {
      content: "\f02e";
      color: $red;
      font: normal normal normal 14px/1 FontAwesome;
      position: absolute;
      top: -3px;
      right: 30px;
      font-size: 48px;
    }
    h3 {
      color: $red !important;
    }
    .list {
      li {
        i {
          color: $red !important;
        }
      }
    }
    .theme_btn {
      background: $red !important;
    }
  }
}

/* End Server Price Area css
============================================================================================ */

/*pricing-area-two 
================================================*/
.pricing-area-two {
  padding: 100px 0px;
  .main_title {
    margin-bottom: 63px;
  }
  .pricing-item {
    background: #fff;
    padding: 50px 30px;
    transition: all 0.4s linear;
    h2 {
      font: 300 24px/26px $mon;
      color: $dip;
      text-transform: uppercase;
      letter-spacing: 0.48px;
      margin-bottom: 28px;
    }
    h3 {
      font-family: $mon;
      color: $blue;
      font-size: 28px;
      line-height: 26px;
      font-weight: 700;
      sub {
        font-weight: 400;
        font-size: 14px;
      }
    }
    .list {
      margin-top: 40px;
      li {
        font: 400 14px/28px $pop;
        color: $pfont;
        i {
          color: $blue;
          padding-right: 15px;
        }
      }
    }
    &:hover {
      box-shadow: 0px 6px 11.83px 1.17px rgba(0, 0, 0, 0.08);
    }
  }
}

.price_seven {
  // background: url(../img/domain-bg.jpg) no-repeat fixed center center;
  background-size: cover;
  position: relative;
  z-index: 3;
  &:before {
    content: "";
    background: rgba(125, 185, 232, 0.9);
    position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    z-index: -1;
    display: block;
  }
}
.pricing-item.price_item3 {
  padding: 0px;
  .price_header {
    text-align: center;
    // background: url(../img/price-head-1.jpg) no-repeat scroll center center;
    background-size: cover;
    position: relative;
    z-index: 2;
    color: #fff;
    padding-top: 32px;
    padding-bottom: 32px;
    box-shadow: 0px 4px 12.35px 0.65px rgba(0, 0, 0, 0.1);
    &:before {
      content: "";
      background: rgba(63, 63, 63, 0.95);
      position: absolute;
      top: 0px;
      left: 0px;
      width: 100%;
      height: 100%;
      z-index: -1;
      @include transition;
    }
    &:after {
      content: "\f02e";
      color: #fff;
      font: normal normal normal 14px/1 FontAwesome;
      position: absolute;
      top: -3px;
      right: 20px;
      font-size: 48px;
      opacity: 0;
      @include transition;
    }
    h2 {
      color: #fff;
    }
    h3 {
      color: #fff;
    }
  }
  .price_body {
    padding: 35px 30px;
    text-align: center;
    .list {
      margin-top: 0px;
      text-align: left;
    }
    .theme_btn {
      background: #3f3f3f;
    }
  }
  &:hover,
  &.active {
    .price_header {
      &:before {
        background: rgba(125, 185, 232, 0.9);
      }
      &:after {
        opacity: 1;
      }
    }
    .price_body {
      .theme_btn {
        background: #3498db;
      }
    }
  }
}

