/*============== subcribe_area_two css ============*/
.subcribe_area_two {
  padding: 100px 0px;
  .subscrib_inner {
    .input-group {
      max-width: 615px;
      margin: 75px auto auto;
      display: block;
      position: relative;
      width: 100%;
      input {
        height: 66px;
        padding: 0px 172px 0px 35px;
        border-radius: 31px;
        border: 1px solid #e0e0e0;
        border-right: 0px;
        -webkit-box-shadow: none;
        box-shadow: none;
        outline: none;
        background: transparent;
        width: 100%;
        display: block;
        @include placeholder {
          font-family: $pop, FontAwesome;
          font-size: 18px;
          font-weight: normal;
          letter-spacing: 0.36px;
          color: $pfont;
        }
      }
      .input-group-btn {
        display: block;
        white-space: normal;
        position: absolute;
        right: 0px;
        width: 180px;
        border-radius: 31px;
        top: 0px;
        -webkit-box-shadow: 0px 4px 12.35px 0.65px rgba(0, 0, 0, 0.12);
        box-shadow: 0px 4px 12.35px 0.65px rgba(0, 0, 0, 0.12);
        .btn-default {
          height: 66px;
          padding: 0px;
          box-shadow: none !important;
          outline: none !important;
          border-radius: 0px;
          border: none;
          background: transparent;
          position: relative;
          width: 180px;
          text-align: center;
          border-radius: 31px;
          z-index: 3;
          color: #fff;
          background: $blue;
          font-family: "Poppins", sans-serif;
          letter-spacing: 0.48px;
          font-size: 24px;
          text-transform: uppercase;
          font-weight: normal;
        }
      }
    }
  }
}
/*============== subcribe_area_two css ============*/

