/* Nine Price Area css
============================================================================================ */
.nine_price_area {
  margin-top: 250px;
  padding-bottom: 130px;
  .nine_title {
    padding-bottom: 130px;
  }
}
.nine_price_inner {
}
.nine_price_item {
  text-align: center;
  background: #f8f8f8;
  border-bottom: 2px solid #000;
  padding: 55px 25px;
  h2 {
    text-transform: uppercase;
    color: #272727;
    font-family: $hee;
    font-weight: bold;
    font-size: 28px;
    margin-bottom: 30px;
    padding-bottom: 10px;
    border-bottom: 1px solid #d7d7d7;
    display: inline-block;
    span {
      font-size: 18px;
    }
  }
  ul {
    text-align: left;
    background: #fff;
    padding: 40px 35px 50px 35px;
    border-bottom: 1px solid #000;
    li {
      margin-bottom: 10px;
      a {
        font-size: 15px;
        color: #585858;
        font-family: "sans-serif";
        font-style: italic;
        i {
          padding-right: 10px;
        }
      }
      &:last-child {
        margin-bottom: 0px;
      }
    }
  }
  h4 {
    font-size: 30px;
    color: #272727;
    font-family: "sans-serif";
    font-weight: bold;
    font-style: italic;
    padding: 30px 0px;
    span {
      font-family: $hee;
      color: #8e8e8e;
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
    }
  }
  .get_btn {
    background: #d7d7d7;
    text-transform: uppercase;
    font-size: 14px;
    color: #272727;
    border-radius: 4px;
    padding: 0px 35px;
    line-height: 37px;
    display: inline-block;
    font-family: $hee;
    @include transition;
    &:hover {
      background: #eb484d;
      color: #fff;
    }
  }
  &:hover {
    border-color: #eb484d;
    ul {
      border-color: #eb484d;
    }
  }
}
/* End Nine Price Area css
============================================================================================ */

/* Ten Price Area css
============================================================================================ */
.ten_price_item_box {
}
.ten_price_item {
  padding: 0px 10px;
  margin-top: 160px;
  position: relative;
  border-radius: 15px;
  .ten_price_item_box {
    background: #fafcff;
    position: relative;
    top: -160px;
    margin-bottom: -120px;
    border-radius: 15px;
    padding-bottom: 60px;
    overflow: hidden;
    box-shadow: 0px 20px 20px 0px rgba(0, 0, 0, 0.16);
    &:before {
      content: "Exclusive";
      color: #fff;
      position: absolute;
      right: -27px;
      top: 22px;
      font-size: 12px;
      font-family: $hee;
      text-transform: uppercase;
      padding: 0px 30px;
      transform: rotate(45deg);
    }
    .ten_p_head {
      text-align: center;
      padding-top: 60px;
      padding-bottom: 55px;
      h3 {
        font-size: 22px;
        font-family: $hee;
        padding-bottom: 10px;
      }
      h2 {
        color: #272727;
        font-family: $hee;
        font-weight: bold;
        font-size: 45px;
        span {
          color: #8e8e8e;
          font-size: 15px;
          font-weight: normal;
        }
      }
    }
    ul {
      margin-left: 32px;
      padding-left: 15px;
      li {
        margin-bottom: 13px;
        a {
          font-size: 16px;
          color: #8e8e8e;
          font-family: $hee;
          font-weight: normal;
        }
        &:last-child {
          margin-left: 0px;
        }
      }
    }
  }
  .ten_p_btn {
    text-align: center;
    display: block;
    font-size: 22px;
    color: #fff;
    font-family: $hee;
    font-weight: bold;
    padding-bottom: 30px;
  }
}
.red {
  background: #ff6767;
  .ten_p_head {
    h3 {
      color: #ff6767;
    }
  }
  .ten_price_item_box {
    &:before {
      background: #ff6767;
    }
    ul {
      border-left: 1px dashed #ff6767;
    }
  }
}
.blue {
  background: #0083ff;
  .ten_p_head {
    h3 {
      color: #0083ff;
    }
  }
  .ten_price_item_box {
    &:before {
      background: #0083ff;
    }
    ul {
      border-left: 1px dashed #0083ff;
    }
  }
}
.green {
  background: #2cc990;
  .ten_p_head {
    h3 {
      color: #2cc990;
    }
  }
  .ten_price_item_box {
    &:before {
      background: #2cc990;
    }
    ul {
      border-left: 1px dashed #2cc990;
    }
  }
}
/* End Ten Price Area css
============================================================================================ */

/* Gadget Product Area css
============================================================================================ */
.g_product_area {
  .g_p_slider {
    margin-top: -30px;
    .owl-stage-outer {
      padding: 30px 0px;
    }
    .item {
    }
    .owl-item {
      opacity: 0.35;
      &.center {
        opacity: 1;
        box-shadow: 0px 0px 68px 0px rgba(0, 0, 1, 0.08);
      }
    }
    .owl-dots {
      margin-top: 35px;
      .owl-dot {
        width: 18px;
        height: 8px;
        background: #d7d7d7;
        border-radius: 4px;
        &.active {
          background: #e9003f;
          width: 36px;
        }
      }
    }
  }
}

.review_gadget_area {
}
.review_img_inner {
  position: relative;
  text-align: center;
  padding-top: 60px;
  .g_br_img {
    position: relative;
    display: inline-block;
    ul {
      li {
        position: absolute;
        font-size: 14px;
        color: #8e8e8e;
        font-family: $hee;
        @include transition;
        span {
          height: 16px;
          width: 20px;
          border-radius: 50%;
          border: 4px solid #eff0f2;
          display: inline-block;
          background: #a5acb7;
          position: relative;
          right: -7.5px;
          top: 3px;
          @include transition;
        }
        &:first-child {
          left: -75px;
          top: 50%;
          transform: translateY(-50%);
        }
        &:nth-child(2) {
          right: -75px;
          top: 50%;
          transform: translateY(-50%);
          span {
            left: -7px;
          }
        }
        &:last-child {
          left: 0px;
          bottom: -40px;
          width: 100%;
          text-align: center;
          span {
            left: 35px;
            top: -26px;
          }
        }
        &:hover {
          color: #e9003f;
          cursor: pointer;
          span {
            background: #e9003f;
            border-color: #f8ced9;
          }
        }
      }
    }
  }
  .main_img {
    position: absolute;
    top: 0px;
    left: 51.5%;
    transform: translateX(-50%);
  }
}

/* End Gadget Product Area css
============================================================================================ */
