/* Header Banner css
============================================================================================ */
.header_banner_area {
  // background: url(../img/home-3/header-bg.jpg) no-repeat scroll center center;
  background-size: cover;
  position: relative;
  z-index: 3;
  &:before {
    content: "";
    background: rgba(252, 90, 26, 0.91);
    position: absolute;
    left: 0px;
    top: 0px;
    height: 100%;
    width: 100%;
    z-index: -1;
  }
}
.header_banner_inner {
  text-align: center;
  padding-top: 210px;
  h3 {
    font-size: 48px;
    font-family: $mon;
    font-weight: bold;
    color: #fff;
    letter-spacing: 0.96px;
    text-transform: uppercase;
    padding-bottom: 20px;
  }
  p {
    font-size: 36px;
    font-family: $mon;
    font-weight: 300;
    text-transform: uppercase;
    color: #fff;
    letter-spacing: 0.72px;
    padding-bottom: 50px;
  }
  .subscrib_inner {
    max-width: 530px;
    margin: 0px auto 75px;
    width: 100%;
    overflow: hidden;
    .input-group {
      display: block;
      position: relative;
      overflow: hidden;
      input {
        height: 68px;
        padding: 0px 172px 0px 35px;
        border-radius: 31px;
        border: 1px solid #e0e0e0;
        border-right: 0px;
        -webkit-box-shadow: none;
        box-shadow: none;
        outline: none;
        background: #fff;
        display: block;
        width: 100%;
        display: block;
        @include placeholder {
          font-family: $pop, FontAwesome;
          font-size: 18px;
          font-weight: normal;
          letter-spacing: 0.36px;
          color: $pfont;
        }
      }
      .input-group-btn {
        display: block;
        white-space: normal;
        position: absolute;
        right: 2px;
        width: 124px;
        border-radius: 31px;
        top: 2.5px;
        -webkit-box-shadow: 0px 4px 12.35px 0.65px rgba(0, 0, 0, 0.12);
        box-shadow: 0px 4px 12.35px 0.65px rgba(0, 0, 0, 0.12);
        .btn-default {
          height: 63px;
          padding: 0px;
          box-shadow: none !important;
          outline: none !important;
          border-radius: 0px;
          border: none;
          position: relative;
          width: 124px;
          text-align: center;
          border-radius: 31px;
          z-index: 3;
          color: #fff;
          background: $red;
          font-family: "Poppins", sans-serif;
          letter-spacing: 0.48px;
          font-size: 20px;
          text-transform: uppercase;
          font-weight: normal;
        }
      }
    }
  }
}
/* End Header Banner css
============================================================================================ */

