/* Blog ms Banner Area css
============================================================================================ */
.blog_banner_area {
  // background: url(../img/banner/blog-banner.jpg) no-repeat scroll center center;
  background-size: cover;
}
.blog_banner_inner {
  padding-top: 140px;
  padding-bottom: 40px;
  h4 {
    font-size: 60px;
    font-family: $hee;
    font-weight: 900;
    color: #fff;
    padding-bottom: 50px;
    span {
      font-weight: normal;
    }
  }
  ul {
    background: #e9003f;
    padding: 0px 15px;
    display: inline-block;
    li {
      display: inline-block;
      margin-right: 5px;
      a {
        font-size: 14px;
        color: #fff;
        font-family: $hee;
        font-weight: normal;
        position: relative;
        line-height: 30px;
        &:before {
          content: "/";
          color: #fff;
          font-size: 14px;
          position: absolute;
          left: -7px;
          top: 50%;
          transform: translateY(-50%);
        }
      }
      &:first-child {
        a {
          &:before {
            display: none;
          }
        }
      }
    }
  }
}
/* End Blog ms Area css
============================================================================================ */

